import { Component, OnInit } from '@angular/core';
import { EventProviderService } from 'src/services/event-provider.service';
import { Storage } from '@ionic/storage';
import { LoaderService } from 'src/services/loader.service';
import { ToastService } from 'src/services/toast.service';
import { ApiService } from 'src/services/api.service';
import { Location } from "@angular/common";
import {TranslateService} from "@ngx-translate/core";
import {ActivatedRoute, NavigationEnd, Route, NavigationExtras, Router, RoutesRecognized} from '@angular/router';
@Component({
  selector: 'app-report-now',
  templateUrl: './report-now.page.html',
  styleUrls: ['./report-now.page.scss'],
})
export class ReportNowPage implements OnInit {
  ch_id: any;
  subject: any = '';
  email: any = '';
  description: any = '';
  divClassForEmail: any;
  account_donor_id: any;
  divClassForSubject: string;
  divClassForDescription: string;
  optiona = 'abcd';
  options: any[] = [
    { value: 'Report', label: 'Report an issue' },
    { value: 'Testimony', label: 'Share a testimony' },
    { value: 'Feature', label: 'Request a feature' },
    // Add more options as needed
  ];
  divClassForCategory: string;
  imageSrc: string | ArrayBuffer | null = null;
   uploadData : any;
 feedback: any = '';
  category: any = '';
 name: any = '';

  constructor(private storage: Storage,
    public event_provider: EventProviderService,
    private loader: LoaderService,
    public toast: ToastService,
    private api: ApiService,
    private router: Router, 
    private location: Location,
    private translate: TranslateService,) { }

   // ZB changes start
   ngOnInit() {
    this.event_provider.getChurchData.subscribe(async (res: any) => {
      if ((Object.keys(res).length === 0)) {
        return;
      }

      this.ch_id = res.church.ch_id;
    });
    this.getUser();
  }
  getUser() {
    // Getting User from local storage
    this.storage.get('user').then(res => {
      if (res != null && res != 'guest') {
        this.account_donor_id = res.user.id;
      }

    });
  }




 
  onReportSubmit(){

    
    if(this.email === '' || !(this.email.match("[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{3,}"))){

      this.divClassForEmail = 'input-error';
    }else if(this.subject === ''){

      this.divClassForSubject = 'input-error';
    }else if(this.description === ''){
      this.divClassForDescription = 'input-error';
    }else if(this.subject !== '' && this.email !== '' && this.description !== ''){

      let body =JSON.stringify(
        {
          email: this.email,
          subject: this.subject,
          description: this.description,
          church_id: this.ch_id,
          // account_donor_id: this.account_donor_id ? this.account_donor_id : ''
         }
      )

      this.loader.presentLoading().then(()=>{
        this.api.reportFunction(body).subscribe((res:any)=>{
          if(res.status){
            this.loader.stopLoading();
            this.toast.presentToastWithOptions('Reported Successfully','Thanks','success');
           // this.router.navigate(['/home/'+ this.ch_id]);
         //  this.location.back();
          //  console.log('status is: ', res);
          }else{
            this.toast.presentToastWithOptions('Error','Something Went Wrong','warning');
          }
        });
        // this.loader.stopLoading();
      });
      
    }
  }
  
  onKeyForSubject(event){
    this.divClassForSubject = '';

  }


  onKeyForEmail(event){

    this.divClassForEmail = '';
  }

  onKeyForDescription(event){

    this.divClassForDescription = '';
  }

  onKeyForCategory(event){
    if(this.category !== ''){
      this.divClassForCategory = '';
    }
  }
  gotobackPage(){
    this.location.back();
  }

  async onFileSelected(event: any){
    // console.log('event is: ', event);
    const file = event.target.files[0];
    // console.log('file is: ', file);
    const reader = new FileReader();
    const toBase64 = file => new Promise((resolve, reject) => {
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.imageSrc = reader.result as string; // Set the imageSrc variable
        resolve(reader.result);
      };
      reader.onerror = error => reject(error);
    });
    this.uploadData = await toBase64(file);
    // console.log('base64 is: ', this.uploadData);
  }
  cancelImage(){
    this.uploadData = ''
    this.imageSrc = ''
    const fileInput = document.getElementById('custom-file-upload') as HTMLInputElement;
  fileInput.value = ''; // Reset the value of the file input
  }
  
  
}
